<template>
    <div class="pa-favorite-wrapper">
        <ul class="nav">
            <li
                class="nav__item"
                v-for="item in tabs"
                :key="item.id"
            >
                <ButtonWithCount
                    :activeClass="item.tab === activeTab"
                    className="nav__link"
                    :text="item.text"
                    :amount="item.amount"
                    @on-click="changeActiveTab(item.tab)"
                />
            </li>
        </ul>
        <keep-alive>
            <component :is="showActiveTab"></component>
        </keep-alive>
    </div>
</template>

<script>
import ButtonWithCount from '@/components/Buttons/ButtonWithCount'
import PerformancesTab from '@/components/FavoritePage/FavoritePerformances'
import ActorsTab from '@/components/FavoritePage/FavoriteActors'
import TheatresTab from '@/components/FavoritePage/FavoriteTheatres'

export default {
    components: {
        ButtonWithCount, PerformancesTab, ActorsTab, TheatresTab
    },
    data () {
        return {
            tabs: [
                {   id: 1,
                    tab: 'performances',  
                    text: 'Вистави',
                    amount: 10
                },
                {   id: 2,
                    tab: 'actors',
                    text: 'Актори',
                    amount: 20
                },
                {   id: 3,
                    tab: 'theatres',  
                    text: 'Театри',
                    amount: 3
                }
            ],
            activeTab: 'performances'
        }
    },
    methods: {
        changeActiveTab: function(tab) {
            this.activeTab = tab
        }
    },
    computed: {
        showActiveTab: function() {
            return this.activeTab[0].toUpperCase() + this.activeTab.slice(1) + 'Tab'
        }
    }
}
</script>

<style lang="scss" scoped>
.nav {
    display: flex;
    justify-content: center;
    padding: 20px 0;

    &__item {

        &:last-child {
            & .nav-link {
                margin-right: 0;
            }
        }
    }
}
.nav__link {
    font-size: var(--title-font-size);
}
</style>