<template>
    <div class="popup" :class="{popup_show: showPopup}">
        <div class="popup__wrapper">
            <transition name="popup-layout">
                <PopupLayout
                    @on-click="onConfirm"
                    v-if="showPopup"
                />
            </transition>
            <transition name="remove-card-popup">
                <div
                    class="popup-block"
                    v-if="showPopup"
                >
                    <button
                        class="popup-block__close"
                        @click="onConfirm(false)"
                    >
                        <CloseIcon />
                    </button>
                    <h3 class="popup-block__title">
                        {{ title }}
                    </h3>
                    <div class="popup-confirm">
                        <Button
                            :text="dismissButtonText"
                            type="transparent"
                            className="popup-confirm__dismiss"
                            @on-click="onConfirm(false)"
                        />
                        <Button
                            :text="acceptButtonText"
                            type="blue"
                            @on-click="onConfirm(true)"
                        />
                    </div>
                </div>
            </transition>
        </div>
    </div>
</template>

<script>
import Button from '@/components/Buttons/Button'
import PopupLayout from '@/components/Popups/PopupLayout'
import CloseIcon from '@/components/SvgComponents/CloseIcon'

export default {
    components: {
        Button, CloseIcon, PopupLayout
    },
    props: ['title', 'dismissButtonText', 'acceptButtonText', 'showPopup'],
    methods: {
        onConfirm: function(confirm) {
            this.$emit('on-click', confirm)
        }
    }
}
</script>

